:global .ant-layout {
  min-height: 100%;
}
:global .ant-layout-content {
  margin: 0px 16px !important;
  overflow: visible !important;
  overflow: initial !important;
  background: transparent !important;
  padding: 0 !important;
}
.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.trigger:hover {
  color: #1890ff;
}
.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  line-height: 32px;
  text-align: center;
  margin: 16px;
  border-radius: 3px;
}
