.login {
  flex-direction: column;
  height: 100vh;
  overflow: auto;
  background: #f0f2f5;
  background-image: url(../img/login-bg.png);
  background-repeat: no-repeat;
  background-position: center 110px;
  background-size: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login .content .header p {
  font-size: 33px;
  color: rgba(0, 0, 0, 0.85);
  font-family: "Myriad Pro", "Helvetica Neue", Arial, Helvetica, sans-serif;
  font-weight: 600;
  position: relative;
  top: 2px;
}
.login .content .main {
  width: 368px;
}
.login .content .main :global .ant-tabs-bar {
  text-align: center;
}
.login .content .main :global .ant-tabs-nav {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.login .content .main :global .ant-input {
  line-height: 40px;
  height: 40px;
  font-size: 16px;
}
.login .content .main :global .ant-input-prefix {
  font-size: 16px;
}
.login .content .main :global .ant-input-affix-wrapper .ant-input:not(:first-child) {
  padding-left: 34px;
}
.login .content .main .forget {
  cursor: pointer;
  color: #1890ff;
}
.login .content .main .forget:hover {
  color: #40a9ff;
}
/* 通用样式, banner demo 单独使用都需要这块样式 */
.banner-user {
  height: 220px;
}
.banner-user-elem {
  text-align: center;
  color: #fff;
  position: relative;
  overflow: hidden;
}
.banner-user-elem .banner-user-title {
  font-size: 32px;
  top: 40%;
}
.banner-user-elem .banner-user-text {
  top: 40%;
}
.banner-anim-elem .bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.antMotion-queue .queue-demo {
  width: 60%;
  margin: auto;
  position: relative;
}
.antMotion-queue .queue-demo .buttons {
  text-align: center;
}
.antMotion-queue .queue-demo .demo-header {
  width: 100%;
  background: #364768;
  height: 28px;
  line-height: 28px;
  border-radius: 6px 6px 0 0;
  position: relative;
}
.antMotion-queue .queue-demo .demo-header .demo-logo {
  display: inline-block;
  margin-left: 10px;
}
.antMotion-queue .queue-demo .demo-header .demo-logo img {
  vertical-align: middle;
}
.antMotion-queue .queue-demo .demo-header .demo-logo img:nth-child(2) {
  margin-left: 5px;
}
.antMotion-queue .queue-demo .demo-header ul {
  float: right;
}
.antMotion-queue .queue-demo .demo-header ul li {
  width: 50px;
  height: 8px;
  background: #4d5c76;
  display: inline-block;
  margin-right: 10px;
}
.antMotion-queue .queue-demo .demo-banner {
  background: #364d79;
  height: 87px;
  position: relative;
}
.antMotion-queue .queue-demo .demo-banner .point {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 5px;
}
.antMotion-queue .queue-demo .demo-banner .point ul li {
  width: 6px;
  height: 6px;
  background: hsla(0, 0%, 100%, 0.15);
  display: inline-block;
  border-radius: 6px;
  margin: 0 5px;
}
.antMotion-queue .queue-demo .demo-page {
  height: 130px;
  position: relative;
}
.antMotion-queue .queue-demo .demo-page > h1 {
  width: 50px;
  height: 8px;
  background: rgba(1, 155, 240, 0.35);
  margin: 10px auto;
}
.antMotion-queue .queue-demo .demo-page > p {
  display: block;
  overflow: hidden;
}
.antMotion-queue .queue-demo .demo-page > p:after,
.antMotion-queue .queue-demo .demo-page > p:before {
  content: "";
  display: block;
  height: 4px;
  background: rgba(1, 155, 240, 0.35);
}
.antMotion-queue .queue-demo .demo-page > p:after {
  width: 60px;
  margin: 5px auto;
}
.antMotion-queue .queue-demo .demo-page > p:before {
  width: 220px;
  margin: auto;
}
.antMotion-queue .queue-demo .demo-page .box {
  margin-top: 10px;
  overflow: hidden;
}
.antMotion-queue .queue-demo .demo-page .box ul li {
  width: 25%;
  margin: 0 4.16667%;
  height: 65px;
  position: relative;
  display: inline-block;
  background: rgba(1, 155, 240, 0.55);
  border-radius: 6px;
}
.antMotion-queue .queue-demo .demo-page .box ul li:before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 6px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.antMotion-queue .queue-demo .demo-footer {
  height: 40px;
  background: #364768;
  border-radius: 0 0 6px 6px;
  overflow: hidden;
}
.antMotion-textAnim .combined-wrapper {
  padding: 0;
  background: linear-gradient(135deg, #329fff 1%, #8c00ff 100%);
  height: 300px;
  position: relative;
  width: 100%;
}
.antMotion-textAnim .combined-reload {
  position: absolute;
  bottom: 16px;
  right: 16px;
}
.antMotion-textAnim .combined {
  color: #fff;
  width: 316px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.antMotion-textAnim .combined-shape {
  position: relative;
}
.antMotion-textAnim .combined-shape > * {
  width: 50%;
  height: 64px;
  position: absolute;
  overflow: hidden;
}
.antMotion-textAnim .combined-shape > * > * {
  width: 100%;
  height: 100%;
  background: #fff;
}
.antMotion-textAnim .combined-shape .shape-left {
  left: 0;
}
.antMotion-textAnim .combined-shape .shape-right {
  right: 0;
}
.antMotion-textAnim .combined .title {
  font-size: 58px;
  letter-spacing: 8px;
  position: absolute;
  width: 350px;
  overflow: hidden;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.antMotion-textAnim .combined-bar {
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
  top: 70px;
}
.antMotion-textAnim .combined .content {
  font-size: 12px;
  text-align: center;
  position: absolute;
  top: 78px;
}
.d3-tip {
  font-size: 12px;
  line-height: 1;
  font-weight: bold;
  padding: 12px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 2px;
  line-height: 1.5;
}
/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  box-sizing: border-box;
  display: inline-block;
  font-size: 14px;
  width: 100%;
  line-height: 1;
  color: rgba(0, 0, 0, 0.8);
  content: "\25BC";
  position: absolute;
  top: -2px;
  text-align: center;
}
.d3-tip.n:after {
  margin: -3px 0 0 0;
  top: 100%;
  left: 0;
}
.bar-chart--simple .axis {
  font-size: 14px;
}
@keyframes mouth {
  0% {
    transform: rotate(-80deg);
    stroke-dasharray: 60, 95;
    stroke-dashoffset: 0;
  }
  40% {
    transform: rotate(280deg);
    stroke-dasharray: 60, 95;
    stroke-dashoffset: 0;
  }
  70%,
  100% {
    transform: rotate(280deg);
    stroke-dashoffset: -23;
    stroke-dasharray: 42, 95;
  }
}
.gutter-box {
  padding: 5px 0;
}
.no-padding .ant-card-body {
  padding: 0 !important;
}
